.card {
  width: 250px;
  height: 300px;
  margin: 16px;
  display: grid;
  grid-template-rows: 200px 1fr 1fr;
  background: white;
  -webkit-box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
}

.card .image_link {
  grid-area: 1/1/1/1;
}

.card .image_link .card_image {
  height: 200px;
  width: 250px;
}

.card .card_description {
  margin-left: 10px;
  margin-top: 15px;
  font-size: 18px;
}

.card .card_created_at {
  margin-left: 10px;
  font-size: 14px;
}

.card .card_price {
  grid-area: 1/1/1/1;
  margin: 8px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: white;
  padding: 2px;
  border-radius: 4px;
}


